<template>
  <div class="container">
    <!-- Sign In -->
    <div class="container__form container--signin">
      <form class="form" @click="eliminate">
        <h2 class="form__title">XY数据迁移</h2>
        <input type="text" placeholder="User" class="input" />
        <input type="password" placeholder="Password" class="input" />
        <button class="btn" @click.stop="login" id="login" type="button">
          登 录
        </button>
        <p class="link common" @click.stop="visitor">游客模式</p>
      </form>
    </div>
    <!-- 对话框内容 -->
    <el-dialog
      v-model="insertStatus"
      style="
        width: 350px;
        position: absolute;
        top: 50%;
        left: 50%;
        border-radius: 10px;
        background-color: #6e84a9;
        opacity: 0.7;
        transform: translate(-50%, -100%);
      "
      title="游客"
      draggable
    >
      <el-col :span="25">
        <el-input placeholder="请输入邀请码" v-model="query"></el-input>
      </el-col>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="insertStatus = false">取 消</el-button>
          <el-button type="primary" @click="visitorSuccess">确 定</el-button>
        </span>
      </template>
    </el-dialog>
    <div v-show="data.LoginShow" id="verify">
      <slide-verify
        ref="block"
        class="verify"
        :l="128"
        :r="32"
        :w="1600"
        :h="1200"
        :slider-text="data.text"
        :accuracy="data.accuracy"
        :imgs="data.imgs"
        @success="onSuccess"
        @refresh="onRefresh"
        @fail="onFail"
      ></slide-verify>
    </div>
    <!-- Overlay -->
    <div class="container__overlay">
      <div class="overlay"></div>
    </div>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import SlideVerify, { SlideVerifyInstance } from "vue3-slide-verify";
import "vue3-slide-verify/dist/style.css";
import { ref, onMounted, onUnmounted, reactive, watch } from "vue";

export default {
  name: "Login",
  components: {
    SlideVerify,
  },

  setup() {
    //定义data变量
    const store = useStore();
    const router = useRouter();
    const block = ref(null);
    const query = ref("");
    const insertStatus = ref(false);
    let data = reactive({
      LoginShow: false,
      visitor: false,
      text: "向右滑动->>>",
      accuracy: 6,
      imgs: [],
    });
    const modulesFiles = require.context("@/assets/verify");
    for (const v in modulesFiles.keys()) {
      data.imgs.push(modulesFiles(modulesFiles.keys()[v]));
    }
    // vue生命钩子
    onMounted(() => {
      document.getElementById("app").classList.add("login");
    });
    onUnmounted(() => {
      document.getElementById("app").classList.remove("login");
    });

    //定义方法
    const eliminate = () => {
      if (document.getElementById("login") != event.target) {
        data.LoginShow = false;
      }
    };
    const login = () => {
      data.LoginShow = true;
      data.visitor = false;
    };
    const visitor = () => {
      data.LoginShow = true;
      data.visitor = true;
    };
    const visitorSuccess = () => {
      if (query.value == "123") {
        router.push("/");
      }
    };
    const onSuccess = (times) => {
      setTimeout(() => {
        block.value.refresh();
      }, 350);
      data.text = `验证通过, 耗时${(times / 1000).toFixed(1)}s`;
      if (data.visitor) {
        insertStatus.value = true;
        data.LoginShow = false;
      } else {
        router.push("/");
      }
    };
    function loginSuccess() {}
    const onRefresh = () => {};
    const onFail = () => {
      data.text = "验证不通过";
    };

    watch(
      () => data.LoginShow,
      (newValue, oldValue) => {
        if (!newValue) {
          data.text = "向右滑动->>>";
          block.value.refresh();
        }
      }
    );
    return {
      data,
      block,
      insertStatus,
      query,
      visitorSuccess,
      visitor,
      login,
      onRefresh,
      onSuccess,
      eliminate,
      onFail,
    };
  },
};
</script>
<style>
.verify {
  left: 30%;
  z-index: 200;
  margin-top: 40px;
}
.login {
  /* COLORS */
  --white: #e9e9e9;
  --gray: #333;
  --blue: #0367a6;
  --lightblue: #008997;

  /* RADII */
  --button-radius: 0.7rem;

  /* SIZES */
  --max-width: 758px;
  --max-height: 420px;

  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  align-items: center;
  background-color: var(--white);
  background-color: #b3ccdf;
  /* 决定背景图像的位置是在视口内固定，或者随着包含它的区块滚动。 */
  /* https://developer.mozilla.org/zh-CN/docs/Web/CSS/background-attachment */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  height: 100vh;
  place-items: center;
}
.el-input {
  border-radius: 200px;
  background: #97b5e8;
}

.common {
  background: linear-gradient(90deg, #2ea2ea 0%, var(--lightblue) 74%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}
.form__title {
  user-select: none;
  font-weight: 300;
  margin: 0;
  margin-bottom: 3rem;
  background: linear-gradient(90deg, var(--blue) 0%, var(--lightblue) 74%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: transparent;
}

.link {
  cursor: pointer;
  font-size: 0.01rem;
  margin: 0.3rem 0;
  user-select: none;
}

.container {
  background-color: var(--white);
  border-radius: var(--button-radius);
  box-shadow: 0 0.9rem 1.7rem rgba(0, 0, 0, 0.25),
    0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
  height: var(--max-height);
  max-width: var(--max-width);
  overflow: hidden;
  position: relative;
  width: 100%;
}

.container__form {
  height: 100%;
  position: absolute;

  top: 0;
  transition: all 0.6s ease-in-out;
  /* height: 350px; */
}

.container--signin {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container__overlay {
  height: 100%;
  left: 50%;
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: transform 0.6s ease-in-out;
  width: 50%;
  z-index: 3;
}

.overlay {
  background-color: var(--lightblue);
  background: url("../assets/login.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  left: -100%;
  position: relative;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
  width: 200%;
}

.btn {
  background-color: var(--blue);
  background-image: linear-gradient(
    90deg,
    var(--blue) 0%,
    var(--lightblue) 74%
  );
  border-radius: 20px;
  border: 1px solid var(--blue);
  color: var(--white);
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  padding: 0.9rem 4rem;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.form > .btn {
  margin-top: 2.5rem;
}

.btn:active {
  transform: scale(0.95);
}

.btn:focus {
  outline: none;
}

.form {
  background-color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 3rem;
  height: 100%;
  text-align: center;
}

.input {
  background-color: #fff;
  border: none;
  padding: 0.9rem 0.9rem;
  margin: 0.5rem 0;
  width: 100%;
}

/* 滑动验证码 样式覆盖 */
.slide-verify-block {
  width: 51px;
  height: 300px;
}
canvas {
  width: 400px;
  height: 300px;
}
</style>
